@import url("https://fonts.googleapis.com/css2?family=Concert+One&display=swap");

:root {
  --font-primary: "Concert One", cursive;
  --clr-bg-primary: #fdd7e4;
  --clr-bg-secondary: #fff2f7;
  --clr-text-primary: #36454f;
  background-color: var(--clr-bg-primary);
}

::selection {
  background-color: var(--clr-bg-primary);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: var(--font-primary);
  color: var(--clr-text-primary);
  font-size: 1.4rem;
}

.appContainer {
  margin: 0 auto;
  width: 95%;
  max-width: 700px;
  height: 75vh;
  min-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

p {
  padding: 1em;
  padding-bottom: 2em;
  background-color: var(--clr-bg-secondary);
  border-radius: 10px;
  white-space: pre;
  margin-bottom: 2em;
}

textarea {
  border-radius: 10px;
  outline: none;
  resize: none;
  font-family: var(--font-primary);
  font-size: 1.4rem;
  color: var(--clr-text-primary);
  background-color: var(--clr-bg-secondary);
  border: none;
  width: 100%;
  padding: 1em;
}

.display {
  position: relative;
}

.clipIcon {
  color: var(--clr-bg-primary);
}

.clipIcon:hover,
.clipIcon:focus {
  color: var(--clr-text-primary);
  transform: scale(1.15);
}

.clipIcon:active {
  transform: scale(1);
}

button {
  background-color: transparent;
  border: none;
  position: absolute;
  right: 10px;
  top: 5px;
  cursor: pointer;
}
